@font-face {
    font-family: 'Play-Bold';
    src: url(Play-Bold.eot);
    src: url(Play-Bold.eot?#iefix) format('embedded-opentype'),
         url(Play-Bold.ttf) format('truetype'),
         url(Play-Bold.woff) format('woff'),
         url(Play-Bold.woff2) format('woff2');
    font-weight: normal;
    font-style: normal;
  }

